.Footer{
    background-color: #C3A995;
    width: 100%;
    color: #000;
}


.Shop{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
}
/*
