    padding: 10px 0 20px;
    max-width: 340px;
    margin: 0 auto;
}
*/

.Logo{
    flex-basis: 50%;
}

.FooterWrapper{
    max-width: 450px;
    margin: 0 auto;
}

.Address{
    display: block;
    text-align: center;
    align-items: center;
    position: relative;
    width: 100%;
}

.Address address{
    font-style: normal;
    font-weight: 100;
    font-size: 16px;
}

.Address a{
    display: block;
    font-size: 18px;
    padding: 5px;
    border-radius: 5px;

}

.Footer address span{
    display: block;
}



.Footer h3{
    font-weight: 100;
}

.SocialWrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 12px;
}

.SocialWrap h3{
    width: 100%;
    position: relative;
    padding: 0.75rem 0;
    font-size: 22px;
    margin-bottom: 5px;
    border-bottom:1px solid #493331;
}

.SocialWrap h4{
    font-weight: 100;
}

.Social{
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    align-items: flex-start;
    flex-wrap: wrap;
}

.Social li{
    padding: 0.5rem;
    font-size: 2rem;
    color: #fff;
    width: 4rem;
    height: 4rem;
    border:1px solid;
    border-radius: 50%;
    background-color: #593D3B;
    box-shadow: 2px 2px 6px #000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Social li:not(:last-of-type){
    margin-right: 1rem;
}

.Social li a{
    /*transform: scale(0.5);*/
    display: block;
}

.PhonesWrapper{
    padding: 10px 20px;
    background-color: #F5B700;
    color: #000;
}

.PhonesWrapper h3{
    padding-top: 0;
    font-size: 20px;
}
.Phones{
    padding: 10px 0;;
    width: 100%;
    background-color: #F5E3E0;
    color: #000;
}

.Phones h2{
    color: #593D3B;
    font-weight: 500;
    margin:0 auto 0.5rem;
    padding-bottom: 0.25rem;
    width: fit-content;
    border-bottom: 1px solid;
    font-size: 22px;
}
.Phones h3{
    width: fit-content;
    border-bottom: 1px solid;
    font-size: 16px;
    margin-bottom: 5px;
    padding: 0;
    margin-left: 10px;
    color: #593D3B;
    font-weight: 400;
}

.Phones span{
    margin-right: 1ch
}

.Phones > div{
    display: flex;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.65px;
    justify-content: space-between;
    padding: 5px 8px;
}


.Icon{
    width: 1rem;
    margin-right: 5px;
}

.Place{
    display: flex;
    align-items: center;
    font-size: 14px;
    letter-spacing: 0.2px;
}

.Place > div{
    text-align: left;
}
.Place > div span{
    display: block;
}

.Developer{
    font-size: 12px;
    padding: 0.5rem 0;
    position: relative;
    font-weight: 100;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: #1f1f1f;
    color: #fff;
    margin-top: 1rem;
}

.Developer h6{
    font-size: 16px;
    font-weight: 100;
    margin-left: 1ch;
}

@media screen and (max-width:380px){
    .SocialWrap{
        padding: 0 6px;
    }
}
@media screen and (max-width:365px){
    .Social li{
        padding: 0.25rem;
        font-size: 1.5rem;
        width: 3.5rem;
        height: 3.5rem;
    }
}