body, div, h1, h2, h3, h4, h5, h6, p, ol, ul, blockquote, figure{
  margin:0;
  padding:0;
}
html {
  scroll-behavior: smooth;
}

body{
  position: relative;
  font-family: 'Alegreya', serif, Ubuntu, sans-serif;
  max-width: 2500px;
  margin:0 auto;
  overflow-x: hidden;
  color: var(--textColor);
  background-color: #593D3B;
}

img{
  width: 100%;
  height: auto;
  display: block;
}

a{
  text-decoration: none;
  color: inherit;
}

ul, ol{
  list-style: none;
}

address{
  font-style: normal;
}

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
  max-width: 480px;
  margin: 0 auto;
}

.Intro{
  width: 100%;
  height: 100vh;
  background-color: #593D3B;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  padding: 2rem;
  transition: left 0.5s;
}

.intro-wrapper{
  width: 100%;
  height: 100%;
  border:1px solid #8A7968;
  border-radius: 3px;
  box-shadow: 2px 2px 5px #000;
  background-color: #C3A995;
}

.intro-wrapper .btn{
  padding: 0.5rem 2rem;
  background-color: #593D3B;
  color: #fff;
  text-align: center;
  width: 260px;
  margin: 0 auto;
  border:1px solid #8A7968;
  border-radius: 5px;
  opacity: 0;
  transition: all 0.5s
}

.intro-wrapper .btn.show{
  opacity :1;
}

.Intro.hide{
  left: -200vw;
}

.Intro .logo{
  width: 200px;
  margin:5rem auto 8rem;
  opacity: 0;
  transition: all 0.5s
}

.Intro .logo.show{
  opacity: 1;
}

.App.hide{
  opacity: 0;
}