.MenuGrid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 1rem;
    margin-top: 1rem;
  }

  .Manager{
    background-color: #808275;
    color: #fff;
    margin-bottom: 10px;
  }

  .Manager h4{
    font-weight: 300;
    text-align: center;
    padding: 5px;
  }

  .Manager h4 span{
    display: block;
  }

  .Alert{
    background-color: #BC471B;
    color: #fff;
    margin-bottom: 10px; 
    padding: 6px 12px; 
  }

  .Alert h5{
    margin:0 auto;
    border: 1px solid #fff;
    font-weight: 300;
    padding: 6px;
  }