.ProductListItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /*padding: 0 4px 4px;*/
    flex-wrap: nowrap;
    background-color: rgba(255,255,255, 0.8);
    margin-bottom: 6px;
    color: #000;
}

.ProductListItemImage{
    width: calc(100% + 16px);
    height: 250px;
    /*margin-bottom: 1rem;*/
    left: -8px;
    position: relative;
}

.ProductListItemImage img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: 100%;
}

.ProductListItemName{
    max-width: calc(100% - 7ch);
    font-size: 18px;
    line-height: 22px;
    text-align: left;
    position: relative;
    font-weight: 400;
    padding: 4px;
}

.ProductListItemName img{
    position: absolute;
    width: 60px;
    top: 0;
    left: 95%;
    z-index: 5;
}




