.ProductListItemPrice{
    align-self: stretch;
    background-color: #8A7968;
    color: #fff;
    padding: 5px;
    /*border: 1px solid;*/
    position: relative;
    z-index: 20;
    font-size: 19px;
    font-weight: 300;
    display: flex;
    align-items: center;
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    align-self: center;
    justify-content: center;
    margin: 5px; 
    box-shadow: 2px 2px 3px #a1a1a1;
}


.ProductListItemPrice:after{
    content: '';
    width: 0%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: #2B954A;
    position: absolute;
    z-index: -1;
    transition: width 0.5s;
    border-radius: 2px;
}

.ProductListItemPrice.TakeAway:after{
    width: 100%;
}

.ProductListItemBottle{
   /* width: 100%;
    text-align: right;*/
    font-size: 14px;
    font-style: italic;
    position: relative;
    padding-left: 3px;
    margin-left: 5px;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    border-left: 1px solid #048BA8;
}
.ProductListItemBottle:before{
    /*
    content: "φιάλη";
    font-size: 10px;
    position: absolute;
    bottom: -8px;
    left: 12px;
    */
}

.ProductListItemBottle span{
    font-size: 10px;
}

.ProductListItemDescription{
    width: 100%;
    font-family: sans-serif;
    font-size: 12px;
    line-height: 15px;
    padding-left:0px;
    position: relative;
    margin-top: 2px;
    padding-top: 2px;
    border-top: 1px solid #8A7968;;
}

.ProductListItemDescription:before{
    content: '';
    position: absolute;
    width: 100%;
    border-top: 1px solid var(--priceColor);
    left: 0;
    top: 0;
}

@media screen and (max-width:362px){
    .ProductListItemName{
        font-size: 16px;
        line-height: 20px;
    }
}
