.Header{
    display: flex;
    align-items: center;
    padding: 1rem;
    color: #fff;
    background-color: #493331;
    justify-content: center;
    border-bottom: 2px solid #fff;
}

.Header h2{
    font-weight: 500;
    letter-spacing: 0.5px;
}

@media screen and (max-width:350px){
    .Header h2{
        font-size: 20px;    
    }
}