.MenuGrid{
    width: 100%;
    margin-top: 8px;
}

.MenuGridInner{
    display: flex;
    flex-wrap: wrap;
}

.GridItem{
    flex: 0 0 50%;
    transition: all 0.3s;
    position: relative;
}

.Shrink .GridItem{
    flex: 0 0 25%;
    filter: grayscale(1);
}

.GridItem:before,
.GridItem:after{
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 0;
    background-color: #fff;
    transition: height 0.1s;
}

.GridItem:before{    
    top: -8px;
}
.GridItem:after{
    top: 100%;
}

.GridItem.Selected:before,    
.GridItem.Selected:after{
    height: 8px;    
}


.GridItem.Selected{
    filter: grayscale(0);
}


.GridItem h2{
    position: absolute;
    font-size: 18px;
    text-align: center;
    width: 100%;
    height: 100%;
    color: #fff;
    font-weight: 400;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0 ,0, 0.2);
    padding: 0 1rem;
    text-shadow: 1px 1px 2px #000;
}
.Shrink h2{
    display: none;
}


.GridItem.Collapse{
    flex: 0 0 25vw;
}

.GridItem img{
    width: 100%;
    object-fit: cover;
    aspect-ratio: 1;
    display: block;

}

.CategoryTitle{
    width: 100%;
    margin-top: 8px;
    padding: 0.625rem;
    border-top: 4px solid #fff;
    border-bottom: 2px solid #fff;
    font-size: 1.5rem;
    background-color: #C3A995;
}