.CategoryTab{
    border:2px solid #C3A995;
    width: 100%;
    margin-bottom: 1rem;
    overflow: hidden;
    position: relative;
    background-color: #8A7968;
}

.CategoryInnerWrapper{
    display: flex;
    align-items: center;
    height: calc((60vw - 2rem) / (16 / 9));
}

.TabImg{
    flex: 0 0 60%;
    height: 100%;   
    box-shadow: 2px 2px 5px #593D3B;
}

.TabImg img{
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
}

.CategoryTab h2{
    flex: 0 0  40%;
    color: #fff;
    padding: 10px;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    letter-spacing: 0.18px;
}

@media screen and (max-width: 400px) {
    .CategoryTab h2{
        padding: 5px;
        letter-spacing: 0.5px;
    }
}

